import React, { useState, useEffect } from 'react';
import {
    Box,
    Container,
    Typography,
    Paper,
    Alert,
    Divider
} from '@mui/material';
import { useProxyWatch } from '@fanfanlo';
import { user } from '@app/model';
import { autoWebViewJs } from '@droid/android';

import FallDetectionWrapper from './FallDetectionWrapper';
import ShakeDetectionWrapper from './ShakeDetectionWrapper';
import { SENSOR_MONITORING_STATE_CHANGED } from './constants';

const EmergencyCallSettings: React.FC = () => {
    // UI展开状态管理
    const [fallExpanded, setFallExpanded] = useState(false);
    const [shakeExpanded, setShakeExpanded] = useState(false);
    
    // 状态管理现在由各自的Wrapper组件内部处理，这里只需要UI展开状态

    // 监听用户登录状态
    const [userToken] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);
    const isLoggedIn = !!userToken;

    // 确保同一时间只有一个功能展开
    const handleToggleExpanded = (
        type: 'fall' | 'shake', 
        expanded: boolean
    ) => {
        if (expanded) {
            // 如果要展开，先收起其他的
            setFallExpanded(type === 'fall');
            setShakeExpanded(type === 'shake');
        } else {
            // 如果要收起，只收起当前的
            switch (type) {
                case 'fall':
                    setFallExpanded(false);
                    break;
                case 'shake':
                    setShakeExpanded(false);
                    break;
            }
        }
    };

    // 状态管理和事件监听现在由各自的Wrapper组件内部处理

    return (
        <Container maxWidth="md" sx={{ py: 3 }}>
            {/* 页面标题 */}
            <Paper sx={{ p: 3, mb: 3, textAlign: 'center' }}>
                <Typography 
                    variant="h4" 
                    component="h1" 
                    sx={{ 
                        mb: 1, 
                        fontWeight: 700,
                        background: 'linear-gradient(45deg, #ff6b6b, #4ecdc4)',
                        backgroundClip: 'text',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent'
                    }}
                >
                    🚨 紧急呼救设置
                </Typography>
                <Typography 
                    variant="body1" 
                    color="text.secondary"
                    sx={{ mb: 2 }}
                >
                    配置您的紧急求助功能，确保关键时刻能及时获得帮助
                </Typography>
                
                {!isLoggedIn && (
                    <Alert severity="info" sx={{ mt: 2 }}>
                        <Typography variant="body2">
                            部分功能需要登录后才能使用，建议先完成登录
                        </Typography>
                    </Alert>
                )}
            </Paper>

            {/* 功能模块 */}
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                {/* 摔倒检测 */}
                <FallDetectionWrapper
                    title="📱 摔倒呼救"
                    isExpanded={fallExpanded}
                    onToggleExpanded={(expanded) => handleToggleExpanded('fall', expanded)}
                    animationType="fall"
                />

                {/* 摇一摇检测 */}
                <ShakeDetectionWrapper
                    title="🤳 摇一摇呼救"
                    isExpanded={shakeExpanded}
                    onToggleExpanded={(expanded) => handleToggleExpanded('shake', expanded)}
                    animationType="shake"
                />
            </Box>

            {/* 使用说明 */}
            {/* <Paper sx={{ p: 3, mt: 4, backgroundColor: 'rgba(0,0,0,0.02)' }}>
                <Typography variant="h6" gutterBottom sx={{ fontWeight: 600 }}>
                    💡 使用说明
                </Typography>
                <Divider sx={{ mb: 2 }} />
                <Box component="ul" sx={{ pl: 2, m: 0 }}>
                    <Typography component="li" variant="body2" sx={{ mb: 1, lineHeight: 1.6 }}>
                        <strong>摔倒呼救</strong>：通过手机传感器检测意外跌倒，自动触发紧急呼救，包含防误触设置和语音配置
                    </Typography>
                    <Typography component="li" variant="body2" sx={{ mb: 1, lineHeight: 1.6 }}>
                        <strong>摇一摇呼救</strong>：快速摇动手机触发紧急求助，支持自定义检测参数和语音设置
                    </Typography>
                </Box>
                <Alert severity="warning" sx={{ mt: 2 }}>
                    <Typography variant="body2">
                        <strong>重要提醒</strong>：请在非紧急情况下测试各功能，确保在真正需要时能正常工作
                    </Typography>
                </Alert>
            </Paper> */}
        </Container>
    );
};

export default EmergencyCallSettings;