import React, { useState, useEffect, useRef } from 'react';
import {
    Box,
    Paper,
    Typography,
    IconButton,
    Collapse,
    Switch,
    FormControlLabel,
    useTheme,
    alpha,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Button
} from '@mui/material';
import {
    ExpandLess,
    ExpandMore,
    Animation as AnimationIcon
} from '@mui/icons-material';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';
import { authService } from '@src/services/authService';
import ShakeDetectionSettings from './ShakeDetectionSettings';
import { VoiceEmergencySettingsRef } from './components/VoiceEmergencySettings';
import { useProxyWatch } from '@fanfanlo';
import { user } from '@app/model';
import _ from 'lodash';

interface ShakeDetectionWrapperProps {
    title: string;
    isExpanded: boolean;
    onToggleExpanded: (expanded: boolean) => void;
    animationType?: 'fall' | 'shake';
}

const SHAKE_CONFIG_KEY = "shake_emergency_config";

// 摇一摇配置接口
interface ShakeConfig {
    enabled: boolean;
    shakeCount: number;
    timeWindowMs: number;
    emergencyType: string;
    threshold: number;
    vibrationFeedback: boolean;
    soundFeedback: boolean;
    actions: Array<{
        id: string;
        type: 'sound' | 'vibration';
        duration: number;
        resourceId?: string;
    }>;
    maxWaitDuration: number;
    version: number;
}

// 默认配置
const defaultShakeConfig: ShakeConfig = {
    enabled: false,
    shakeCount: 3,
    timeWindowMs: 2000,
    emergencyType: "medical",
    threshold: 0.5,
    vibrationFeedback: true,
    soundFeedback: false,
    actions: [],
    maxWaitDuration: 10000,
    version: 1
};

const ShakeDetectionWrapper: React.FC<ShakeDetectionWrapperProps> = ({
    title,
    isExpanded,
    onToggleExpanded,
    animationType
}) => {
    const theme = useTheme();

    // 摇一摇状态管理
    const [shakeConfig, setShakeConfig] = useState<ShakeConfig>(defaultShakeConfig);
    const [shakeConfigLoaded, setShakeConfigLoaded] = useState(false);
    
    // 复制自 ShakeDetectionSettings - 检测用户登录状态
    const [userToken] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);
    const isLoggedIn = !!userToken;
    const voiceSettingsRef = useRef<VoiceEmergencySettingsRef>(null);
    
    // 监听展开状态变化，展开时重新加载VoiceEmergencySettings配置
    useEffect(() => {
        if (isExpanded && voiceSettingsRef.current) {
            voiceSettingsRef.current.reloadConfig();
        }
    }, [isExpanded]);

    // 从存储加载摇一摇配置
    function loadShakeConfig(): ShakeConfig {
        const conf = autoJsStoreUtils.read(SHAKE_CONFIG_KEY, {});
        const config = _.merge(defaultShakeConfig, conf);
        return config;
    }

    // 保存摇一摇配置到Android端
    function saveShakeConfigToAndroid(newConfig: ShakeConfig) {
        try {
            console.log('保存摇一摇配置到Android端:', newConfig);
            
            // 验证配置有效性
            if (!validateShakeConfig(newConfig)) {
                console.error('配置参数无效，不进行保存');
                return;
            }

            // 构建配置JSON
            const configJson = JSON.stringify(newConfig);
            
            // 编码为Base64
            const base64Config = btoa(unescape(encodeURIComponent(configJson)));
            
            // 通过Rhino调用Android方法
            const script = `
                var result = com.fanfanlo.emergencycall.controller.ShakeManager.updateShakeConfig("${base64Config}");
                result;
            `;
            const { javaResultString } = autoWebViewJs.callScript(script);
            console.log('摇一摇配置保存结果:', javaResultString);
            
        } catch (error) {
            console.error('保存摇一摇配置失败:', error);
        }
    }

    // 验证配置参数的有效性
    function validateShakeConfig(config: ShakeConfig): boolean {
        return (
            config.shakeCount >= 1 && config.shakeCount <= 10 &&
            config.timeWindowMs > 0 && config.timeWindowMs <= 10000 &&
            ['medical', 'security'].includes(config.emergencyType) &&
            typeof config.threshold === 'number' &&
            config.threshold >= 0 &&
            config.version > 0
        );
    }

    // 初始化加载配置
    useEffect(() => {
        const config = loadShakeConfig();
        setShakeConfig(config);
        setShakeConfigLoaded(true);
    }, []);

    // 处理摇一摇开关切换
    async function handleToggleShakeDetection(enabled: boolean) {
        if (enabled) {
            // 检查登录状态
            if (!isLoggedIn) {
                const appLoggedIn = await authService.showLoginDialog();
                if (!appLoggedIn) {
                    alert("未登录无法使用该功能");
                    return;
                }
            }
        }
        
        // 更新配置
        const newConfig = { ...shakeConfig, enabled };
        setShakeConfig(newConfig);
        
        // 保存到Android端
        saveShakeConfigToAndroid(newConfig);
    }

    // 动画占位组件
    const AnimationPlaceholder: React.FC<{ type?: 'fall' | 'shake' }> = ({ type }) => {
        const getAnimationContent = () => {
            switch (type) {
                case 'fall':
                    return {
                        icon: '🚶‍♂️',
                        title: '摔倒检测',
                        description: '检测到意外跌倒时自动触发紧急呼救',
                        color: theme.palette.error.main
                    };
                case 'shake':
                    return {
                        icon: '📱',
                        title: '摇一摇报警',
                        description: '快速摇动手机触发紧急呼救',
                        color: theme.palette.warning.main
                    };
                default:
                    return {
                        icon: '🚨',
                        title: '紧急呼救',
                        description: '紧急情况下的自动求助功能',
                        color: theme.palette.primary.main
                    };
            }
        };

        const content = getAnimationContent();

        return (
            <Box sx={{ 
                textAlign: 'center',
                color: 'text.secondary',
                px: 4
            }}>
                <Box sx={{ 
                    fontSize: 72, 
                    mb: 2,
                    animation: 'pulse 2s infinite',
                    '@keyframes pulse': {
                        '0%': { transform: 'scale(1)' },
                        '50%': { transform: 'scale(1.1)' },
                        '100%': { transform: 'scale(1)' }
                    }
                }}>
                    {content.icon}
                </Box>
                <Typography 
                    variant="h5" 
                    sx={{ 
                        mb: 1, 
                        fontWeight: 600,
                        color: content.color
                    }}
                >
                    {content.title}
                </Typography>
                <Typography 
                    variant="body1" 
                    sx={{ 
                        color: 'text.secondary',
                        lineHeight: 1.6
                    }}
                >
                    {content.description}
                </Typography>
                <Box sx={{
                    mt: 3,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    gap: 1,
                    color: 'text.disabled'
                }}>
                    <AnimationIcon sx={{ fontSize: 20 }} />
                    <Typography variant="caption">
                        点击展开设置
                    </Typography>
                </Box>
            </Box>
        );
    };

    return (
        <Paper 
            sx={{ 
                mb: 2, 
                overflow: 'hidden',
                border: `1px solid ${alpha(theme.palette.primary.main, 0.2)}`,
                '&:hover': {
                    boxShadow: theme.shadows[4]
                }
            }}
        >
            {/* 标题栏 */}
            <Box 
                sx={{ 
                    p: 2, 
                    display: 'flex', 
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    backgroundColor: alpha(theme.palette.primary.main, 0.05),
                    '&:hover': {
                        backgroundColor: alpha(theme.palette.primary.main, 0.1)
                    }
                }}
            >
                {/* 左侧：标题和开关 */}
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                    <Typography 
                        variant="h6" 
                        sx={{ 
                            fontWeight: 600,
                            color: theme.palette.primary.main
                        }}
                    >
                        {title}
                    </Typography>
                    
                    {/* 启用/禁用开关 */}
                    {shakeConfigLoaded && (
                        <FormControlLabel
                            control={
                                <Switch
                                    checked={shakeConfig.enabled}
                                    onChange={(e) => handleToggleShakeDetection(e.target.checked)}
                                    size="small"
                                    color="primary"
                                />
                            }
                            label={
                                <Typography variant="body2" color="text.secondary">
                                    {shakeConfig.enabled ? '已启用' : '未启用'}
                                </Typography>
                            }
                            onClick={(e) => e.stopPropagation()}
                            sx={{ margin: 0 }}
                        />
                    )}
                </Box>

                {/* 右侧：展开/收起按钮 */}
                <IconButton 
                    size="small"
                    onClick={() => onToggleExpanded(!isExpanded)}
                    sx={{ 
                        color: theme.palette.primary.main,
                        transform: isExpanded ? 'rotate(180deg)' : 'rotate(0deg)',
                        transition: 'transform 0.2s ease-in-out'
                    }}
                >
                    <ExpandMore />
                </IconButton>
            </Box>
            
            {/* 内容区 */}
            <Box>
                {/* 动画区域 - 折叠时显示 */}
                <Collapse in={!isExpanded}>
                    <Box sx={{ 
                        height: 300,
                        width: '100%',
                        display: 'flex', 
                        alignItems: 'center', 
                        justifyContent: 'center',
                        backgroundColor: alpha(theme.palette.background.paper, 0.8),
                        borderTop: `1px solid ${theme.palette.divider}`,
                        position: 'relative',
                        overflow: 'hidden'
                    }}>
                        <AnimationPlaceholder type={animationType} />
                    </Box>
                </Collapse>
                
                {/* 设置区域 - 展开时显示 */}
                <Collapse in={isExpanded}>
                    <Box sx={{ 
                        borderTop: `1px solid ${theme.palette.divider}`,
                        backgroundColor: theme.palette.background.paper
                    }}>
                        <ShakeDetectionSettings voiceSettingsRef={voiceSettingsRef} />
                    </Box>
                </Collapse>
            </Box>
        </Paper>
    );
};

export default ShakeDetectionWrapper;