import { autoWebViewJs } from "@droid/android";
import { Box, Button } from "@mui/material";
import { trackingPrint } from "@app/model";


export function TestEmergencyCall() {
    function onClick(){
        // 测试呼救按钮点击埋点
        trackingPrint({params:{
            type:"用户操作",
            title:"测试功能",
            subtitle:"测试呼救-按钮点击",
            extra:{
                timestamp: Date.now(),
                isWebView: !!(autoWebViewJs && typeof autoWebViewJs.callScript === 'function')
            }
        }});
        
        try {
            // const script = `
    //         var instance = com.fanfanlo.emergencycall.service
    // .SensorMonitoringService.callFallDetection();
    //         `
            const script = `
            com.fanfanlo.emergencycall.manager.EmergencyCallJSInterface.startFallDetectionCall()`
            const res = autoWebViewJs.callScript(script);
            console.log("TestEmergencyCall res=", res);
            
            // 测试呼救执行成功埋点
            trackingPrint({params:{
                type:"系统事件",
                title:"测试功能",
                subtitle:"测试呼救-执行成功",
                extra:{
                    timestamp: Date.now(),
                    scriptResult: res
                }
            }});
        } catch (error) {
            // 测试呼救执行异常埋点
            trackingPrint({params:{
                type:"系统异常",
                title:"测试功能",
                subtitle:"测试呼救-执行异常",
                extra:{
                    timestamp: Date.now(),
                    error: error instanceof Error ? error.message : String(error),
                    stack: error instanceof Error ? error.stack : undefined
                }
            }});
        }
    }
    return(
        <Box sx={{ 
            display: 'flex', 
            justifyContent: 'center', 
            alignItems: 'center',
            p: 3
        }}>
            <Button  
                variant="contained" 
                onClick={onClick}
                sx={{
                    backgroundColor: '#FF5722',
                    color: 'white',
                    fontSize: '18px',
                    fontWeight: 'bold',
                    minHeight: '64px',
                    minWidth: '200px',
                    borderRadius: '32px',
                    boxShadow: '0 8px 24px rgba(255, 87, 34, 0.3)',
                    textTransform: 'none',
                    position: 'relative',
                    overflow: 'hidden',
                    '&:hover': {
                        backgroundColor: '#E64A19',
                        transform: 'translateY(-2px)',
                        boxShadow: '0 12px 32px rgba(255, 87, 34, 0.4)',
                    },
                    '&:active': {
                        transform: 'translateY(0px)',
                        boxShadow: '0 4px 16px rgba(255, 87, 34, 0.3)',
                    },
                    '&::before': {
                        content: '""',
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        width: '0',
                        height: '0',
                        borderRadius: '50%',
                        backgroundColor: 'rgba(255, 255, 255, 0.3)',
                        transform: 'translate(-50%, -50%)',
                        transition: 'width 0.3s, height 0.3s',
                    },
                    '&:hover::before': {
                        width: '100%',
                        height: '100%',
                    }
                }}
                startIcon={
                    <Box sx={{ 
                        fontSize: '24px',
                        animation: 'pulse 2s infinite',
                        '@keyframes pulse': {
                            '0%': { transform: 'scale(1)' },
                            '50%': { transform: 'scale(1.1)' },
                            '100%': { transform: 'scale(1)' }
                        }
                    }}>
                        🚨
                    </Box>
                }
            >
                测试呼救
            </Button>
        </Box>  
    )
}